import Big from "big.js";

/**
 * Response from BMF Lohn- und Einkommensteuerrechner.
 *
 * Response and interface descriptions:
 * - https://www.bmf-steuerrechner.de/interface/einganginterface.xhtml
 * - https://issues.init.de/secure/attachment/708773/708773_2021-11-05-PAP-2022-anlage-1.pdf
 *
 * Only "Ausgangsparameter" of the response are mapped. "Ausgangsparameter DBA" not included.
 *
 * Field descriptions are copied from:
 * https://issues.init.de/secure/attachment/708773/708773_2021-11-05-PAP-2022-anlage-1.pdf
 */
export class BmfSteuerRechnerResponse {
  /**
   * Bemessungsgrundlage fr die Kirchenlohnsteuer in Cent
   */
  BK: Big = new Big(0);

  /**
   * Bemessungsgrundlage der sonstigen Bezge (ohne Vergtung fr
   * mehrjhrige Ttigkeit) fr die Kirchenlohnsteuer in Cent.
   * Hinweis: Negativbetrge, die aus nicht zu besteuernden Vorteilen bei
   * Vermgensbeteiligungen ( 19a Absatz 1 Satz 4 EStG) resultieren,
   * mindern BK (maximal bis 0). Der Sonderausgabenabzug fr
   * tatschlich erbrachte Vorsorgeaufwendungen im Rahmen der
   * Veranlagung zur Einkommensteuer bleibt unberhrt.
   */
  BKS: Big = new Big(0);

  /**
   * Bemessungsgrundlage der Vergtung fr mehrjhrige Ttigkeit und
   * der tarifermigt zu besteuernden Vorteile bei
   * Vermgensbeteiligungen fr die Kirchenlohnsteuer in Cent
   */
  BKV: Big = new Big(0);

  /**
   * Fr den Lohnzahlungszeitraum einzubehaltende Lohnsteuer in Cent
   */
  LSTLZZ: Big = new Big(0);

  /**
   * Fr den Lohnzahlungszeitraum einzubehaltender
   * Solidarittszuschlag in Cent
   */
  SOLZLZZ: Big = new Big(0);

  /**
   * Solidarittszuschlag fr sonstige Bezge (ohne Vergtung fr
   * mehrjhrige Ttigkeit in Cent.
   * Hinweis: Negativbetrge, die aus nicht zu besteuernden Vorteilen bei
   * Vermgensbeteiligungen ( 19a Absatz 1 Satz 4 EStG) resultieren,
   * mindern SOLZLZZ (maximal bis 0). Der Sonderausgabenabzug fr
   * tatschlich erbrachte Vorsorgeaufwendungen im Rahmen der
   * Veranlagung zur Einkommensteuer bleibt unberhrt.
   */
  SOLZS: Big = new Big(0);

  /**
   * Solidarittszuschlag fr die Vergtung fr mehrjhrige Ttigkeit und
   * der tarifermigt zu besteuernden Vorteile bei
   * Vermgensbeteiligungen in Cent
   */
  SOLZV: Big = new Big(0);

  /**
   * Lohnsteuer fr sonstige Bezge (ohne Vergtung fr mehrjhrige
   * Ttigkeit und ohne tarifermigt zu besteuernde Vorteile bei
   * Vermgensbeteiligungen) in Cent
   * Hinweis: Negativbetrge, die aus nicht zu besteuernden Vorteilen bei
   * Vermgensbeteiligungen ( 19a Absatz 1 Satz 4 EStG) resultieren,
   * mindern LSTLZZ (maximal bis 0). Der Sonderausgabenabzug fr
   * tatschlich erbrachte Vorsorgeaufwendungen im Rahmen der
   * Veranlagung zur Einkommensteuer bleibt unberhrt.
   */
  STS: Big = new Big(0);

  /**
   * Lohnsteuer fr die Vergtung fr mehrjhrige Ttigkeit und der
   * tarifermigt zu besteuernden Vorteile bei Vermgensbeteiligungen
   * in Cent
   */
  STV: Big = new Big(0);

  /**
   * Fr den Lohnzahlungszeitraum bercksichtigte Beitrge des
   * Arbeitnehmers zur privaten Basis-Krankenversicherung und privaten
   * Pflege-Pflichtversicherung (ggf. auch die Mindestvorsorgepauschale)
   * in Cent beim laufenden Arbeitslohn. Fr Zwecke der
   * Lohnsteuerbescheinigung sind die einzelnen Ausgabewerte
   * auerhalb des eigentlichen Lohnsteuerberechnungsprogramms zu
   * addieren; hinzuzurechnen sind auch die Ausgabewerte VKVSONST.
   */
  VKVLZZ: Big = new Big(0);

  /**
   * Fr den Lohnzahlungszeitraum bercksichtigte Beitrge des
   * Arbeitnehmers zur privaten Basis-Krankenversicherung und privaten
   * Pflege-Pflichtversicherung (ggf. auch die Mindestvorsorgepauschale)
   * in Cent bei sonstigen Bezgen. Der Ausgabewert kann auch negativ
   * sein. Fr tarifermigt zu besteuernde Vergtungen fr mehrjhrige
   * Ttigkeiten enthlt der PAP keinen entsprechenden Ausgabewert.
   */
  VKVSONST: Big = new Big(0);
}
