import { YesNo } from "@/globals/js/calculations/model";

interface Props {
  readonly elternteilName: string;
  readonly alleinerziehend?: YesNo | null;
}

export function NotificationBEGResultWasRecalculated({
  elternteilName,
  alleinerziehend,
}: Props) {
  return (
    <div>
      {alleinerziehend !== YesNo.YES
        ? `Das Ergebnis der Berechnung fr ${elternteilName} hat sich verndert, da Sie Angaben im Formular gendert haben.`
        : "Das Ergebnis der Berechnung hat sich verndert, da Sie Angaben im Formular gendert haben."}
    </div>
  );
}
