import Big from "big.js";

export class BmfAbgaben {
  /**
   * Lohnsteuerzahlung
   */
  lstlzz: Big = new Big(0);

  /**
   * Fr den Lohnzahlungszeitraum einzubehaltender Solidarittszuschlag
   */
  solzlzz: Big = new Big(0);

  /**
   * Bemessungsgrundlage fr die Kirchenlohnsteuer
   */
  bk: Big = new Big(0);

  /**
   * Bemessungsgrundlage der sonstigen Bezge (ohne Vergtung fr mehrjhrige Ttigkeit) fr die Kirchenlohnsteuer
   */
  bks: Big = new Big(0);

  /**
   * Bemessungsgrundlage der Vergtung fr mehrjhrige Ttigkeit fr die Kirchenlohnsteuer
   */
  bkv: Big = new Big(0);

  /**
   * Solidarittszuschlag fr sonstige Bezge (ohne Vergtung fr mehrjhrige Ttigkeit)
   */
  solzs: Big = new Big(0);

  /**
   * Solidarittszuschlag fr die Vergtung fr mehrjhrige Ttigkeit
   */
  solzv: Big = new Big(0);

  /**
   * Lohnsteuer fr sonstige Bezge (ohne Vergtung fr mehrjhrige Ttigkeit)
   */
  sts: Big = new Big(0);

  /**
   * Lohnsteuer fr die Vergtung fr mehrjhrige Ttigkeit
   */
  stv: Big = new Big(0);
}
